.who_main {
    background-color: #FAFAFC;
    width: 100%;
    justify-content: center;
    height: 100vh;
  
    hr {
      margin-top: -0.9rem;
      overflow: visible;
      border: none;
      color: #888888;
      background-color: #888888;
      height: 2px;
      width: 30%;
      justify-content: center;
      text-align: center;
      margin-left: 35%;
    }
    .meet_title {
      text-align: center;
      font: normal normal 400 39px/40px IBM Plex Sans;
      color: #162d43;
      margin-top: 2rem;
      span {
        color: #208ce1;
      }
    }
    .meet_content {
      font-size: 14px;
      text-align: center;
      color: #162d43;
      opacity: 1;
      width: 680px;
      justify-content: center;
      margin-left: 20rem;
      margin-right: 20rem;
      margin-top: 1rem;
      line-height: 1.8;
    }
    .meet_row {
      margin-top: 2rem;
    }
    .meet_row2{
        margin-top: 8rem;

    }
    .meet_card {
      width: 260px;
      height: 261px;
      border: 0;
    }
    .meet_card_title {
      font: normal normal medium 24px/30px IBM Plex Sans;
      color: #162d43;
      opacity: 1;
      text-align: center;
    }
    .meet_card_text {
      text-align: center;
      font: normal normal normal 15px/30px IBM Plex Sans;
      color: #162d43;
      opacity: 0.6;
    }
  }
  